<template>
  <div>
    <el-link type="primary" v-show="!isHtml && root != ''" @click="handleHtml(true)"
      >编辑HTML</el-link
    >
    <el-link type="primary" v-show="isHtml && root != ''" @click="handleHtml(false)"
      >返回编辑器</el-link
    >
    <div v-show="!isHtml && root != ''" ref="editorElem"></div>
    <el-input
      v-show="isHtml && root != ''"
      type="textarea"
      :rows="15"
      placeholder="请输入内容"
      v-model="editorContentHtml"
      @change="handleHtmlChange"
    ></el-input>
    <h4 v-show="this.root == ''">没有设置路径“root”...</h4>
  </div>
</template>
<script>
import E from "wangeditor";
import axios from "axios";
import { getToken } from "@/utils/auth";
export default {
  name: "Editor",
  data() {
    return {
      isHtml: false,
      editor: null,
      editorContent: "",
      editorContentHtml: "",
      header: {
        headers: {
          "Content-Type": "multipart/form-data; boundary=--------------------------AAAAA",
          AccessToken: getToken(),
        },
      },
    };
  },
  props: {
    editorContentData: {},
    root: {
      type: String,
      default: "",
    },
  },
  watch: {
    editorContentData(n, o) {
      // console.log(this.editorContent);
      // console.log(n);
      if (!this.editorContent) {
        this.editorContent = n;
        this.editor.txt.html(this.editorContent);
      }
    },
  },
  mounted() {
    this.editor = new E(this.$refs.editorElem);
    // 编辑器的事件，每次改变会获取其html内容
    this.editor.config.onchange = (html) => {
      this.editorContent = html;
      this.catchData(this.editorContent); // 把这个html通过catchData的方法传入父组件
    };

    // 上传图片
    this.editor.config.withCredentials = true;
    //自定义插入图片函数
    // this.editor.customConfig.uploadImgHooks = {
    //   customInsert: function (insertImg, result, editor) {
    //     console.info(result);
    //   },
    // };
    // 自定义上传图片方法
    this.editor.config.customUploadImg = (file, insert) => {
      // files 是 input 中选中的文件列表
      // insert 是获取图片 url 后，插入到编辑器的方法
      let param = new FormData(); // 创建form对象
      param.append("file[]", file[0]);
      param.append("root", this.root);

      const url = "/api/file/uploads"; // url是上传图片的路径
      // 一般项目中都会封装好发送请求得方法,我这为了通用直接用axios
      axios.post(url, param, this.header).then((res) => {
        // res是上传成功后返回的数据,返回的数据中需要有上传图片的路径,
        // 通过insert方法将路径传入,即可将图片在富文本中插入

        // 我这返回的是JSON数据,需要解析
        const data = res.data;
        const imgUrl = data.data.url_path; // 上传后的图片路径
        // 上传代码返回结果之后，将图片插入到编辑器中

        for (let i in imgUrl) {
          this.editor.cmd.do(
            "insertHTML",
            '<img src="' +
              ("https://files.ddhi.cn" + imgUrl[i]) +
              '" style="max-width: 100%;">'
          );
          //insert('"http://oss.ddhi.cn"' + imgUrl[i]);
        }
      });
    };
    this.editor.config.uploadImgMaxSize = 3 * 1024 * 1024; // 图片的大小
    this.editor.config.uploadImgMaxLength = 5; // 上传图片的个数
    this.editor.config.uploadImgShowBase64 = true;
    this.editor.config.uploadFileName = "myFileName";
    this.editor.config.menus = [
      // 菜单配置
      "head", // 标题
      "bold", // 粗体
      "fontSize", // 字号
      "fontName", // 字体
      "italic", // 斜体
      "underline", // 下划线
      "strikeThrough", // 删除线
      "foreColor", // 文字颜色
      "backColor", // 背景颜色
      "link", // 插入链接
      "list", // 列表
      "justify", // 对齐方式
      "quote", // 引用
      "emoticon", // 表情
      "image", // 插入图片
      "table", // 表格
      "code", // 插入代码
      "undo", // 撤销
      "redo", // 重复
    ];
    this.editor.create(); // 创建富文本实例
  },
  methods: {
    // 获取到富文本中的值
    catchData(data) {
      this.$emit("editorData", data);
    },

    handleHtml(v) {
      if (v) {
        this.editorContentHtml = this.editorContent;
      } else {
        //this.editorContent = this.editorContentHtml;
        //this.editor.txt.html(this.editorContent);
      }
      this.isHtml = v;
    },
    handleHtmlChange() {
      this.editorContent = this.editorContentHtml;
      this.editor.txt.html(this.editorContent);
    },
  },
};
</script>
<style>
.w-e-panel-container {
  margin-left: 0px !important;
}
</style>
